<template>
  <div>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column
        prop="id"
        label="规格参数ID"
        width="180"
        show-overflow-tooltip
      >
      </el-table-column>
      <el-table-column
        prop="itemCatId"
        label="产品关联ID"
        width="180"
        show-overflow-tooltip
      >
      </el-table-column>
      <el-table-column
        prop="paramData"
        label="规格参数内容"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <div v-html="scope.row.paramData"></div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
    };
  },
  mounted() {
    this.Pages(1);
    this.$bus.$on("pagePub", (val) => {
      // console.log(val);
      this.Pages(val);
    });
    this.$api.selectItemParamAll().then((res) => {
      // console.log(res.data);
      if (res.data.status === 200) {
        this.tableData = res.data.result;
        this.$bus.$emit('tal',res.data.result.length);
      }
    });
  },
  methods: {
    Pages(page) {
      this.$api.selectTbItemAllByPage({ page }).then((res) => {
        // console.log({ page });
        if (res.data.status === 200) {
          this.tableData = res.data.result;
        }
      });
    },
  },
};
</script>

